.customer {
  position: relative;

  padding-bottom: 60px;
  @media screen and (min-width: 768px) {
    padding-bottom: 80px;
  }
  @media screen and (min-width: 1200px) {
    padding-bottom: 120px;
  }
}

.customer__review {
  margin-left: auto;
  margin-right: auto;
}

.customer__container {
  position: relative;
  color: $black-color;
  max-width: 480px;
  background-color: $reviews-background-color;
  z-index: 0;
  margin-left: auto;
  margin-right: auto;
  background-image: repeating-linear-gradient(
    to bottom,
    $complementary-color 1px,
    $complementary-color 2px,
    $gradient-customs 2px,
    $gradient-customs 33px
  );
  width: 100%;
  height: 100%;
  background-position: center top 74px;
  background-size: calc(100% - 100px) 100%;
  background-repeat: no-repeat;
  @media screen and (min-width: 768px) {
    max-width: 620px;
    background-position: center top 67px;
    background-size: calc(100% - 20px) 100%;
  }
  @media screen and (min-width: 1200px) {
    max-width: 968px;
    background-position: center top 65px;
    background-size: calc(100% - 73px) 100%;
  }
}
.customer__img {
  margin-left: auto;
  margin-right: auto;
  padding-bottom: 32px;
  @media screen and(min-width: 768px) {
    padding-bottom: 20px;
  }
  @media screen and(min-width: 1200px) {
    padding-bottom: 16px;
  }
}
.customer__item {
  text-align: center;
}
.customer__text__comment {
  font-family: $primary-font;
  font-weight: 500;
  font-size: 16px;
  line-height: 2;
  padding-top: 43px;
  padding-right: 58px;
  padding-bottom: 24px;
  padding-left: 65px;

  margin-bottom: 16px;
  letter-spacing: 0.04em;
  // text-decoration: underline 1px solid $complementary-color;
  // text-underline-offset: 8px;
  // background-image: url( repeating-linear-gradient);

  @media screen and (min-width: 768px) {
    padding-top: 30px;
    padding-left: 51px;
    padding-right: 55px;
    padding-bottom: 25px;
    line-height: 2.14;
    margin-bottom: 4px;
  }
  @media screen and (min-width: 1200px) {
    padding-top: 35px;
    padding-left: 109px;
    padding-right: 109px;
    padding-bottom: 30px;
    line-height: 1.81;
    margin-bottom: 16px;
  }
}
