.pagination {
  margin-bottom: 16px;
}
.customer__text {
  font-family: $primary-font;
  font-weight: 500;
  font-size: 20px;
  line-height: 1.86;
  margin-bottom: 16px;
  @media screen and (min-width: 768px) {
    margin-bottom: 32px;
  }
}

.customer__text::before {
  content: '';
  display: block;
  background-image: url(../images/icons/icon-threedots.svg);
  background-size: 32px 4px;
  margin: 0 auto 15px;
  width: 32px;
  height: 4px;
}

.customer__list__svg {
  display: flex;
  justify-content: center;
}
.customer__item__svg:not(:last-child) {
  margin-right: 18px;
}
.icon-threedots-white {
  fill: $reviews2-icon-color;
}

.wrapper {
  margin: 0 auto;
  position: relative;
  width: 100%;
}

.slider {
  height: inherit;
  position: relative;
  width: inherit;
  // overflow: hidden;
}

.slides {
  // height: inherit;
  opacity: 1;
  // position: absolute;
  width: inherit;
  // z-index: 0;
  transform: scale(0.5);
  transition: transform ease-in-out 0.7s, opacity ease-in-out 0.5s;
}

#slide1:checked ~ .slider > .slide1,
#slide2:checked ~ .slider > .slide2,
#slide3:checked ~ .slider > .slide3 {
  opacity: 1;
  z-index: 1;
  transform: scale(1);
}
#slide2:checked ~ .slider > .slide1 {
  opacity: 0;
}
#slide3:checked ~ .slider > .slide2 {
  opacity: 0;
}
#slide3:checked ~ .slider > .slide1 {
  opacity: 0;
}
.slide2,
.slide3 {
  opacity: 0;
  position: absolute;
  top: 0;
  left: 0;
}

.wrapper > input {
  display: none;
}

.wrapper .controls {
  left: 50%;
  transform: translateX(-50%);
  position: absolute;
}

.wrapper label {
  cursor: pointer;
  display: inline-block;
  height: 12px;
  position: relative;
  width: 12px;
  // border-radius: 50%;
  background-image: url(../images/icons/dots.svg);
  transition: background ease-in-out 0.5s;
  background-repeat: no-repeat;
  @media screen and (min-width: 768px) {
  }
}
.wrapper label:not(:last-child) {
  margin-right: 18px;
}
// .wrapper label:checked {
//   display: none;
// }
.wrapper label:hover {
  background-image: url(../images/icons/dotsred.svg);
}
#slide1:checked ~ .controls label:nth-of-type(1),
#slide2:checked ~ .controls label:nth-of-type(2),
#slide3:checked ~ .controls label:nth-of-type(3) {
  background-image: url(../images/icons/icon-home.svg);
  z-index: 10;
}

